import React, { useState } from "react";
import { Menu, MenuItem, Box } from "@mui/material";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import { Themecolors } from "api/Colors";

interface DropDownProps<T> {
  label?: string;
  options: T[];
  onChange?: (value: T | number) => void;
  renderOption?: (option: T) => React.ReactNode;
  padding?: string;
}

const CustomDropDown = <T extends {}>({
  label,
  options = [],
  onChange = (newValue: T) => {},
  renderOption = (option: T) => <>{JSON.stringify(option)}</>,
  padding = "0.3em",
}: DropDownProps<T>) => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const [selectedOption, setSelectedOption] = useState<T | any>(null);

  const handleClick = (event: React.MouseEvent<HTMLElement>) => {
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleChange = (event: React.MouseEvent<HTMLElement>, option: T) => {
    setSelectedOption(option);
    onChange(option);
    handleClose();
  };

  const handleAllOptionClick = (event: React.MouseEvent<HTMLElement>) => {
    setSelectedOption(-1);
    onChange(-1);
    handleClose();
  };

  const renderSelectedOption = () => {
    if (selectedOption === -1) {
      return "All";
    }
    if (!selectedOption) {
      return label || "Select an option";
    }
    return renderOption(selectedOption);
  };

  const optionsWithAll = [
    { label: "All", value: -1 },
    ...options.map((option) => ({
      label: renderOption(option),
      value: option,
    })),
  ];

  return (
    <Box sx={{ padding: padding }}>
      <Box
        sx={{
          cursor: "pointer",
          bgcolor: "gray.500",
          display: "flex",
          alignContent: "center",
          borderRadius: "5px",
          border: "1px solid white",
          padding: "0.5em",
          backgroundColor: "white",
          height: "2.2em",
          "&:hover": {
            backgroundImage: Themecolors.B_hv3,
          },
          alignItems: "center",
          justifyContent: "space-between",
          overflow: "hidden",
          textOverflow: "ellipsis",
          whiteSpace: "nowrap",
        }}
        onClick={handleClick}
      >
        {renderSelectedOption()}
        <ArrowDropDownIcon />
      </Box>

      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={handleClose}
        PaperProps={{
          style: {
            transform: "translateY(-12px)",
            minWidth: "200px",
            opacity: 1,
            transition:
              "opacity 286ms cubic-bezier(0.4, 0, 0.2, 1), transform 190ms cubic-bezier(0.4, 0, 0.2, 1)",
          },
        }}
      >
        {optionsWithAll.map((option: any, index) => (
          <MenuItem
            key={index}
            sx={{
              backgroundColor: "transparent !important",
              "&:hover": {
                backgroundColor: "#f5f5f5 !important",
              },
              "&.Mui-selected": {
                backgroundColor: "transparent !important",
              },
            }}
            onClick={
              option.value === -1
                ? handleAllOptionClick
                : (event) => handleChange(event, option.value)
            }
          >
            {option.label}
          </MenuItem>
        ))}
      </Menu>
    </Box>
  );
};

export default CustomDropDown;
